.container {
  position: relative;
  .leftHolder {
    position: absolute;
    top: 12px;
    bottom: 12px;
    left: 12px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .rightHolder {
    position: absolute;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 12px;
    top: 0;
  }
}
.input {
  border-radius: 6px;
  border: 1px solid var(--light-border, #c1c7de);
  background-color: var(--input-bg, #fff);
  box-sizing: border-box;
  color: var(--text-primary, #1e202c);
  &.small {
    height: 32px;
    padding: 8px 16px;
    font-size: 12px;
    &.round {
      border-radius: 32px;
    }
  }
  &.middle {
    height: 40px;
    padding: 12px 20px;
    font-size: 14px;
    &.round {
      border-radius: 40px;
    }
  }
  &.large {
    height: 48px;
    padding: 14px 24px;
    font-size: 16px;
    &.round {
      border-radius: 48px;
    }
  }
  &.fullWidth {
    width: 100%;
  }
  &.hasLeft {
    padding-left: 48px;
  }
  outline-color: var(--light-focus-border, #0057ff);
  &:invalid {
    border: 1px solid var(--input-invalid-border, #fb2047);
  }
  &:valid:not(:placeholder-shown) {
    border: 1px solid var(--input-valid-border, #2bc128);
  }

  &[type='number']::-webkit-outer-spin-button,
  &[type='number']::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
  }

  &[type='number'] {
    -moz-appearance: textfield; /* Firefox */
  }
}
